// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store/index.js'
import VueResource from 'vue-resource'

Vue.use(VueRouter);
Vue.use(VueResource);
// require('!style-loader!css-loader!less-loader!./assets/css/main.less');

import index from './page/index.vue'
import Creat from './page/Creat/Creat.vue'
import CreatSelect from './page/Creat/CreatSelect.vue'
import CreatFinsh from './page/Creat/CreatFinsh.vue'
import CreatSuccess from './page/Creat/CreatSuccess.vue'
import Goods from './page/Settlement/Goods.vue'
import Settlement from './page/Settlement/Settlement.vue'
document.execCommand("BackgroundImageCache", false, true)
const routes = [
  {path: '/', component: index},
  {path: '/index', component: index},
  {path: '/creat', component: Creat},
  {path: '/CreatSelect', component: CreatSelect},
  {path: '/CreatFinsh', component: CreatFinsh},
  {path: '/CreatSuccess', component: CreatSuccess},
  {path: '/Goods', component: Goods},
  {path: '/Settlement', component: Settlement},
  {path: '*', component: index}
];
const router = new VueRouter({
  routes: routes
});

const data = {
  examTime: 'HTML',//考试的时间
}
/* eslint-disable no-new */
var vm = new Vue({
  router,
  store,
  data,
  template: `<div id="page">
<transition name="fade" mode="out-in">
    <router-view></router-view>
</transition>
</div>
`,
  beforeCreate: function () {
    //和服务器中断连接
    if (this.$store.state.serverIP == 0) {
      router.push({path: '/error'})
    }
    //判断是否为注册用户
    if (this.$store.state.newbi == true) {
      router.push({path: '/index'})
    } else {//欢迎页面
      router.push({path: '/Goods'})
    }
  }
}).$mount('#app');




