<template>
  <transition name="list">
    <div v-show="showFlag" id='food' style="overflow:hidden;">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.ImageUrl" alt="">
          <div @click="hide" class="back"><i class="icon-arrow_lift"></i></div>
        </div>

        <div class="content">
          <h1 class="title">{{food.Name}}</h1>
          <div class="detail">
            <span class="sell-sount">月售666份</span><span class="rating">好评率98%</span>
          </div>
          <div class="price">
            <span class="now">¥{{food.price}}/份</span>
            <span class="old">¥78</span>
          </div>
        </div>
        <transition name="list">
          <div v-show="food.count >0" class="cartcontrol-wrapper">
            <cartcontrol :food="food"></cartcontrol>
          </div>
        </transition>
        <transition name="list">
        <div v-show="!food.count " @click="addFood" class="buy">加入购物车</div>
        </transition>
      </div>
      <split></split>
      <div v-if="food.info" class="info">
        <div class="title">商品信息</div>
        <div class="text">{{food.info}}</div>
      </div>
      <split></split>
      <div class="rating">
        <h1 class="title" >商品评价</h1>
        <ratingselect                       :desc="desc" ></ratingselect>
      </div>
      <div class="rating-wrapper">
        <ul v-show="food.ratings" >
          <li v-show="needShow(rating.rateType,rating.text)" class="rating-item" v-for="rating in food.ratings">
            <div class="user">
              <div class="name">{{rating.username}}
                <div class="avatar">
                  <img width="100%" height="100%"
                       :src="rating.avatar"
                       class="avatar">
                </div>
              </div>
            </div>

            <div @click="text()" class="time">{{ rating.rateTime | time }}   <span :class="{'icon-thumb_up': rating.rateType === 0,
                            'icon-thumb_down':  rating.rateType === 1 }"></span></div>
            <p >
              <p class="text">{{rating.text}}</p>

            </p>
          </li>
          <div style="clear: both"></div>
        </ul>
        <div v-show="!food.ratings "
             class="no-ratings">暂时没有评价!
        </div>
      </div>
      <div style="margin-bottom: 66px"></div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue'
  import cartcontrol from '../../components/cartcontrol/cartcontrol.vue'
  import split from '../../components/split/split.vue'
  import ratingselect from '../../components/ratingselect/ratingselect.vue'

  export default{
    name: 'food',
    props: {
      food: {
        type: Object,
      }
    },
    data: function () {
      return {
        showFlag: false,
        selectType:this.$store.state.selectType,
        onlyContent:this.$store.state.onlyContent,
        desc:{
          all:'全部',
          positive:'吐槽',
          negative:'推荐'
        }
      }
    },
    components: {
      cartcontrol,
      split,
      ratingselect
    },
    methods: {
      show(){
        this.showFlag = true
        this.$store.state.goodsShow = !this.$store.state.goodsShow
      },
      hide(){
        this.showFlag = false
        this.$store.state.goodsShow = !this.$store.state.goodsShow
      },
      addFood(){
        Vue.set(this.food,'count',1)
      },
      text(){
        console.log(this.$store.state.selectType,);
      } ,
      needShow(type, text){
        console.log('type'+type);
        if (this.$store.state.onlyContent && !text)
          return false
        if (this.$store.state.selectType === 2){
          return true
        }
        else{
          return this.$store.state.selectType === type
        }

      }
    }


  }

  Vue.filter('time', function (value) {//value为13位的时间戳
    function add0(m) {
      return m < 10 ? '0' + m : m
    }
    var time = new Date(parseInt(value));
    var y = time.getFullYear();
    var m = time.getMonth() + 1;
    var d = time.getDate();

    return y + '-' + add0(m) + '-' + add0(d);
  });
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../assets/css/icon.less";

  #food {
    .icon-thumb_up{
      color: #1392cf;
    }
    z-index: 10;
    width: 100%;
    background-color: #FFFFFF;
    .title {
      font-size: 18px;
      line-height: 18px;
      margin-bottom: 8px;
      font-weight: 800;
      color: rgb(7, 17, 27);
    }
    .food-content {
      background: #ffffff;
      position: relative;
    }
    .image-header {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 100%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
      .back {
        position: absolute;
        top: 10px;
        left: 5px;
        .icon-arrow_lift {
          display: block;
          padding: 10px;
          font-size: 20px;
          font-weight: 900;
          color: #FFFFFF;
        }
      }
    }
    .content {
      width: 100%;
      padding: 18px;

      .detail {
        margin-bottom: 18px;
        line-height: 10px;
        /*font-size: 0;*/
      }
      .rating {
        margin-right: 12px;
        font-size: 10px;
        color: rgb(153, 153, 159);
      }
      .sell-sount {
        margin-right: 12px;
        font-size: 10px;
        color: rgb(153, 153, 159);
      }
      .price {
        font-weight: 700;
        line-height: 24px;
        .now {
          margin-right: 8px;
          font-size: 14px;
          color: rgb(240, 20, 20);
        }
        .old {
          text-decoration: line-through;
          color: rgb(147, 153, 159);;
          font-size: 11px;
        }
      }
    }
    .cartcontrol-wrapper {
      position: absolute;
      right: 10px;
      bottom: 10px;

    }
    .buy {
      position: absolute;
      right: 18px;
      bottom: 18px;
      z-index: 10;
      height: 24px;
      line-height: 24px;
      font-size: 10px;
      padding: 0 12px;
      box-sizing: border-box;
      border-radius: 12px;
      background-color: #3689ef;
      color: #FFFFFF;
    }
    .info{
      padding: 18px;
      .tltle{
        font-size: 14px;
        line-height: 24px;
        padding: 0 8px;
        color: rgb(7, 17, 27);
      }

    }
    .rating{
      padding: 18px;

    }
    .rating-wrapper{
      width: 100%;margin: 14px;
      .rating-item {
        height: 100px;
        .user {
          position: relative;
          float: right;
          margin-right: 30px;
          margin-top: 10px;
          .name {
            top:0;
            margin-bottom: 10px;
          }
          .avatar {
            width: 50px;

          }
        }
        .time {

        }
        .text{
          font-size: 12px;
        }
      }
    }
    .text{
      line-height: 24px;
      padding: 0 8px;
      font-size: 12px;
      color: rgb(77,85,93);
    }
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s
  }

  .fade-enter,
  .fade-leave-active {
    opacity: 0
  }


  .list-enter-active,
  .list-leave-active
  {
    transition: all 1s;
  }
  .list-enter,
  .list-leave-active
  {
    opacity: 0;
    transform: translateX(100px);
  }

  .icon-thumb_down{
    color: #434a52;
  }
</style>



