    computed: {
      selectFoods(){
        let _foods =[];
        this.products.forEach(function (food) {
          if (food.count) {
            _foods.push(food)
          }
        })
        this.Rice.forEach(function (food) {
          if (food.count) {
            _foods.push(food)
          }
        })
        this.Drink.forEach(function (food) {
          if (food.count) {
            _foods.push(food)
          }
        })
        return _foods;
      },
