<style lang="less" scoped rel="stylesheet/less">
  @import "../../assets/css/variable";
  @width:667;
  @height:375;


  header {
    z-index: 8;
    position:fixed;
    left:0;
    right:0;
    top:0;
    text-align: center;
    background: linear-gradient(45deg, @green 0%, @Green 100%);
    width: 100%;
    height: 45px;
    line-height: 45px;

  }



  #mainbody {
      background-color: #c6c6c6;;
      width: 100%;
      overflow: hidden;
      .main-left {
        position: fixed;
        top: 0;
        margin-top: 45px;
        width: 20%;

      }
      .main-right {
        float: right;
        width: 77%;
        .translation-enter-active{
          transition: all 1s;
        }
        .translation-enter{
          opacity: 0;
          transform: translateX(100px);
        }
      }
      #line{
        float: left;
        width: 3px;
        height: 700px;
        margin-left: -3px;
        background: #236194;
      }

    }

  .left-itam{
    padding: 10px;
    height: 43px;
    line-height: 43px;
  }
  /*未选中*/
  .noactive{
    border-top: 3px solid #a0a4a8;
    border-right: 3px solid  #236194;
    border-bottom: 3px solid #a0a4a8;
    border-left: 3px solid #a0a4a8;
    background-color: #a0a4a8;
  }
    /*选中*/
  .active{

    border-top: 3px solid #236194;
    border-right:3px solid #b9b9b9;
    border-bottom: 3px solid #236194;
    border-left: 3px solid #236194;
  }
  .index{

  }
  #Combos{
    margin-top: 58px;
  }

  #pizzas{
    /*display: none;*/
    position: relative;
    margin-top: 58px;
    .pizzas-item{
      margin-top: 3px;
      border: 1px solid @green;
    }
    h4{
      position: absolute;
      display: inline-block;
      padding-left: 5px;
    }
    .extra{
      position: absolute;
      display: inline-block;
      padding-left: 5px;
      margin-top: 20px;
      font-size: 13px;
    }
    .price{
      position: absolute;
      display: inline-block;
      padding-left: 5px;
      margin-top: 40px;
      font-size: 13px;
      .old{
        text-decoration: line-through;
        color: #93999f;
        font-size: 11px;
      }
    }

  }
  //面饭
  .products{
    margin-top: 58px;
    position: relative;
    .products-item{
      margin-top: 3px;
      position: relative;
      .cartcontrol-wrapper{
        position: absolute;
        right:0 ;
        bottom: 12px;
      }
    }
    h4{
      position: absolute;
      display: inline-block;

    }

    .extra{
      position: absolute;
      display: inline-block;
      padding-left: 5px;
      margin-top: 20px;
      font-size: 13px;
    }
    .price{
      position: absolute;
      display: inline-block;
      padding-left: 5px;
      margin-top: 40px;
      font-size: 13px;
      .now{
        margin-right: 8px;
        font-size: 14px;
        color: rgb(240,20,20);
      }
      .old{
        text-decoration: line-through;
        color: rgb(147,153,159);;
        font-size: 11px;
      }
    }
  }


</style>



