<!--suppress ALL -->
<template>
  <div class="header-md">
    <span class="title"> {{ Title }}</span>
    <span v-show="moreshow"  @click="openMenu" class="menu">
      <i class="icon-list"></i>
    </span>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    name: 'header-md',
    props: ['Title', 'show'],
    computed: {
      moreshow: function () {
        if (this.show == 'y') {
          return this.moreshow = true
        } else {
          return this.moreshow = false
        }
      }/**/
    },
    methods: {//打开菜单
      openMenu: function () {
        this.$store.state.leftmenuShow = true
      }
    }
  }
</script>

<style lang="less" scoped rel="stylesheet/less">
  @import '../assets/css/variable.less';
  @import '../assets/css/menu.css';

  .header-md {
    height: 45px;
    background: linear-gradient(45deg, @green 0%, @Green 100%);
    .title{
      margin-left: 18px; ;
      line-height: 45px;
      font-size: 24px;
    }
    .menu {
      margin-top: 7px;
      margin-right: 7px;
      float: right;
      font-size: 30px;
    }
  }

