<template>
  <div class='ratingselect'>
    <div class="reting-type">
      <span @click="select(2)" :class="{'active':dataselectType[2].show  }" class="block positive">{{desc.all}}</span>
      <span @click="select(1)" :class="{'active':dataselectType[1].show  }" class="block positive">{{desc.positive}}</span>
      <span @click="select(0)" :class="{'active':dataselectType[0].show  }" class="block negative">{{desc.negative}}</span>
    </div>
    <div @click="toggleContent()" class="switch" :class="{'on':this.$store.state.onlyContent,}">
      <span class="icon-check_circle"></span>
      <span class="text">只看有类容的评价</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const  POSITIVE = 0;//积极的
  const  NEGATIVE = 1;// 消极的
  const  ALL = 2; //全部


  export default{
    name: 'ratingselect',
    data: function () {
      return {
        dataselectType: [
          {show:false},
          {show:false},
          {show:true},
        ],
      }
    },
    props: {
      ratings:{
        type:Array,
        default(){
          return []
        }
      },

      desc:{
        type:Object,
        default(){
          return {
            all:'全部',
            positive:'满意',
            negative:'不满意'
          }
        }
      }
    },
    methods: {
      select(type) {
        for (var i in this.dataselectType)
          this.dataselectType[i].show = false
        this.dataselectType[type].show = !this.dataselectType[type].show
        this.$store.state.selectType = type
      },
      toggleContent(){
        this.$store.state.onlyContent = !this.$store.state.onlyContent;
//        console.log(this.$store.state.selectType);
      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../assets/css/icon.less";

  .ratingselect{
    .reting-type{
      padding:  18px 0;
      margin: 0 18px;
      font-size: 0;
      .block{
        display: inline-block;
        vertical-align: top;
        font-size: 12px;
        padding: 8px 12px;
        margin-right: 8px;
        border-radius: 1px;
        color: rgb(77,85,93);
        &.positive{
          background: rgba(0,160,220,0.2);
          &.active{
            background: rgb(0,160,220);
            color: #FFFFFF;
          }
        }
        &.negative{
          background: #ccd0d1;
          &.active{
            background: rgb(77,85,93);
            color: #FFFFFF;
          }
        }
      }
    }
    .switch{
      padding: 3px 18px;
      line-height: 24px;
      border-bottom: 1px solid rgba(7,17,27,.1);
      color: rgb(147,153,159);
      font-size: 0;
      &.on  .icon-check_circle{
        color: #00c850;
      }
      .icon-check_circle{
        margin-right: 4px;
        font-size: 24px;
      }
      .text{
        display:inline-block;
        vertical-align: top;
        font-size: 12px;
      }
    }
  }
</style>



