<template>
  <div class='cartcontrol'>

    <transition name="list">
      <div class="cart-decrease" v-show="food.count>0" @click.stop.prevent="decreaseCart()">
        <span class="icon-remove_circle_outline inner"></span>
      </div>
    </transition>

    <h4 v-show="food.count>0" class="cart-count">{{food.count}}</h4>

    <div @click.stop.prevent="addCart($event)" class="cart-add icon-add_circle"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue'

  export default{
    name: 'cartcontrol',
    props: {
      food: {
        type: Object
      }
    },
    created(){
//      console.log(this.food);
    },
    methods: {
      addCart: function (event) {
        //设置全局 count
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1);
        }
        else {
          this.food.count++;
        }
      },
      decreaseCart: function () {
        if (this.food.count === 0) {
          return 0;
        } else {
          this.food.count--;
        }
      }
    }


  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../assets/css/icon.less";

  .cartcontrol {
    display: inline-block;
    padding: 6px;
    transition: all 0.4s linear;
    .inner {
      display: inline-block;
      line-height: 24px;
      font-size: 24px;
      color: rgb(0, 160, 220);
    }
    .cart-decrease {
      display: inline-block;
      padding: 6px;

    }

    .cart-count {
      display: inline-block;
      vertical-align: top;
      font-size: 10px;
      color: rgb(147, 153, 159);
      line-height: 24px;
      text-align: center;
      padding: 6px 0;
    }
    .cart-add {
      display: inline-block;
      vertical-align: top;
      font-size: 24px;
      color: rgb(0, 160, 220);
      line-height: 24px;
      padding: 6px;
    }

  }
</style>



