<template>
    <div class='shopcart'>
      <div class="content" >
        <div class="content-left" @click="listToggle">
          <div class="logo-wrapper">
            <div class="logo" :class="{'highlight':totelCount > 0 }">
              <i  class="icon-shopping_cart"  :class="{'highlight':totelCount > 0 }"></i>
            </div>
            <div v-show="totelCount > 0" class="num">{{totelCount}}</div>
          </div>
          <div :class="{'highlight':totalPrice > 0 }" class="price">{{totalPrice}}元</div>
          <div class="desc">{{discount}}</div>
          <!--描述结束-->
        </div>
        <!--左侧-->
        <div @click="pay()" class="content-right">
          <div class="pay" v-bind:class="payClass">
            {{payDesc}}
          </div>
        </div>
        <!--结算右侧-->
        <transition name="transHeight">

        <div  v-show="listShow" class="shopcart-list">
          <div @click="hideBackdrop" class="list-header">
            <h1 class="title">购物车</h1>
            <span class="empty" @click="setEmpty()">清空</span>
          </div>
          <div class="list-content">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.Name}}</span>
                <div class="price"> <span>￥{{food.price * food.count}}</span></div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>

        </transition>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  var  STR = '满¥88免外送费';
  import cartcontrol from '../cartcontrol/cartcontrol.vue'
  export default{
    name: 'shopcart',
    data: function () {
      return {
        listShow: false
      }
    },
    components: {
      cartcontrol: cartcontrol
    },
    props: {
      selectFoods: {
        type: Array,
        default: []
      },
      deliveryPrice:{
        type:Number,
        default:0
      },
      minPrice:{
        type:Number,
        default:0
      }
    },
    created() {
//      this.$root.eventHub.$on('cart.add', this.drop)
    },
    computed: {
      //计算总价
      totalPrice: function () {
        let total = 0;
        this.selectFoods.forEach(function (food) {
          total += food.price * food.count
        });
        return total
      },
      //购入数量
      totelCount(){
        let count = 0;
        this.selectFoods.forEach(function (food) {
          count +=  food.count
        });
        return count
      },
      //结算显示
      payDesc(){
        if (this.totalPrice === 0) {
          return `¥${this.minPrice}元起送`;
        }//小于起送价格
        else  if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `还差${diff}元起送`
        }
        else  {
          return '去结算';
        }
      },
      payClass(){//小于起送价格
        if (this.totalPrice < this.minPrice) {
          return 'not-enough'
        } else {
          return 'enough'
        }
      },
      discount(){
        //满20显示88免外送费
        if (this.totalPrice > 20) {
          return STR;
        } else {
          return `另需配送费${this.deliveryPrice}元`;
        }
      }
    },
    methods: {
      hideBackdrop() {
        this.listShow = false
      },
      listToggle() {
        if (!this.selectFoods.length) {
          return
        }
        this.listShow = !this.listShow
      },
      setEmpty() {
        this.selectFoods.forEach((food) => {
          food.count = 0
        })
      },
      pay(){
        if(this.totalPrice < this.minPrice){
          return ;
        }else{
        this.$router.push({path: '/Settlement'})
        }

      }
    }
  }
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../assets/css/icon.less";

  .shopcart{
    z-index: 20;
    position: fixed;
    left:0;
    bottom: 0;
    width: 100%;
    height: 48px;
    .content{
      display: flex;
      background-color: #131822;
      font-size: 0;

      .content-left{
        flex: 1px;
        height: 45px;

        .logo-wrapper{
          display: inline-block;
          position: relative;
          top: -10px;
          margin: 0 12px;
          padding: 6px;
          width: 56px;
          height: 56px;
          box-sizing: border-box;
          vertical-align: top;
          border-radius: 50%;
          background-color: #3b8e6d;
          .logo{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            text-align: center;
            background-color: #d6a0ab;
            &.highlight{
              background: #3793d1;
            }
            .icon-shopping_cart{
              line-height: 44px;;
              font-size: 24px;
              color: #80858a;
              &.highlight{
                color: #FFFFFF;
              }
            }
          }
          .num{
            position: absolute;
            top:0;
            right: 0;
            width: 24px;
            height: 16px;
            line-height: 16px;
            text-align: center;
            border-radius: 16px;
            font-size: 9px;
            font-weight: 700;
            color: #fff;
            background-color: rgb(213, 37, 38);
            box-shadow: 0 4px 8px rgba(0,0,0,0.4);
          }
        }
        .price{
          display: inline-block;
          vertical-align: top;
          margin-top: 12px;
          line-height: 24px;
          /*border:1px solid rebeccapurple;*/
          /*background-color: grey;*/
          padding-right: 12px;
          box-sizing: border-box;
          border-right: 1px solid rgba(255,255,255,0.1);
          font-size: 16px;
          font-weight: 800;
          color: rgba(255, 255, 255, 0.4);
          // 价格高亮
          &.highlight{
            color: #FFFFFF;
          }
        }
        //描述
        .desc{
          display: inline-block;
          vertical-align: top;
          margin: 12px 0 0 12px;
          line-height: 24px;
          font-size: 13px;
          color: rgba(255, 255, 255, 0.4);
          /*background-color: #419979;*/
          /*border: 1px solid red;*/
        }
      }
      .content-right{
        flex: 0 0 0 105px;
        width: 105px;
        background-color: #a0a4a8;
        .pay{
          line-height: 48px;
          text-align: center;
          font-size: 14px;
          font-weight: 800;
          //钱不购的时候
          &.not-enough{
            /*background: #2b333b;*/
          } //钱购的时候
          &.enough{
            background-color: #4ca73d;
            color: #FFFFFF;
          }
        }
      }
    }
    .shopcart-list{
      position: absolute;
      top: 0;
      left :0;
      width :100%;
      background: white;
      transform :translate3d(0,-100%,0);
      z-index: -1;
      &.transHeight-enter-active, &.transHeight-leave-active {
        transition: all 0.5s;
      }
      &.transHeight-enter, &.transHeight-leave-active {
        transform: translate3d(0, 0, 0)
      }
      .list-header{
        height :40px;
        line-height :40px;
        background: #f3f5f7;
        border-bottom :1px solid rgba(7,17,27,0.1);
        .title{
          display :inline-block;
          font-size: 14px;
          font-weight :200;
          color: rgb(7,17,27);
          padding-left: 18px;
        }
        .empty{
          position :absolute;
          right :8px;
          font-size :12px;
          color: rgb(0,160,220);
          padding :0 10px;
        }
      }
      .list-content{
        max-height :100%;
        .food{
          position: relative;
          display :flex;
          height :48px;
          margin: 0 18px;
          border-bottom: 1px solid rgba(7,17,27,0.1);
          .name{
            flex: 1;
            font-size :14px;
            color: rgb(7,17,27);
            line-height :48px;
            font-weight: 700;
          }
          .price{
            font-size: 14px;
            font-weight: 700;
            color :rgb(240,20,20);
            padding: 0 12px 0 18px;
            line-height: 48px;
          }
          .cartcontrol-wrapper{
            font-size: 14px;
            margin-top :6px;
          }

        }
      }

    }

  }
</style>



